const transferDate = (timestamp) =>{
    var newDate = new Date();
    newDate.setTime(timestamp * 1000);
    var month = newDate.getMonth()+1;
    var day = newDate.getDate();
    var hour= newDate.getHours();
    var minute = newDate.getMinutes();
    var timeStr = month < 10 ? '0'+ month : month;
    timeStr += '-';
    timeStr += day < 10 ? '0'+ day : day;
    timeStr += " " +  hour + ":" + minute;
    return timeStr;
}
const getBrowserInfo = () =>{
  return{
        versions:function(){
            var u = navigator.userAgent, app = navigator.appVersion;
            var obj={//
                trident: u.indexOf('Trident') > -1, //IE
                presto: u.indexOf('Presto') > -1, //opera
                webKit: u.indexOf('AppleWebKit') > -1, //
                gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1, //
                mobile: !!u.match(/AppleWebKit.*Mobile.*/)||!!u.match(/AppleWebKit/), //
                ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios
                android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1, //androiduc
                iPhone: u.indexOf('iPhone') > -1 || u.indexOf('Mac') > -1, //iPhoneQQ HD
                iPad: u.indexOf('iPad') > -1, //iPad
                webApp: u.indexOf('Safari') == -1, //web
                weiChart: u.toLowerCase().match(/MicroMessenger/i)=="micromessenger"
            };

            return obj;
        }(),
        language:(navigator.browserLanguage || navigator.language).toLowerCase()
    }
}


export {transferDate,getBrowserInfo};



